.Staff_section{
    width: 100%;
    height: auto;
    background-color:#f7f7f7;
    color:rgba(46, 49, 65,1);
    padding: 100px 0;
}
.staff_card_container{
    display: flex;
    justify-content: center;
    flex-wrap: wrap;
}
.staff_card{
    perspective: 1500px;
    -moz-perspective: 1500px;
    width: 250px;
    height:350px;
    position: relative;
    margin:50px 70px;
}
.card_side{
    height:350px;
    transition: all 0.8s ease;
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    backface-visibility: hidden;
    border-radius: 6px;
    box-shadow: 0 1rem 2rem rgba(0,0,0,.15);
}

.card-front--img{
    border-radius: 4px !important;
    width: 100%;
    height: 87%;
    object-fit: cover;
}
.staff_name{
font-size: 1.2rem;
font-weight: 700;
text-transform: uppercase;
text-align: center;
}
.card_side-front{
    /* background-color: #f7f7f7; */
}
.card_side-back{
    transform: rotateY(-180deg);    
    color:white;
    padding: 10px 15px 20px 15px;
    text-align: center;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-between;
}
.card_side-back-1{
    background-image: linear-gradient(to right bottom, #189AB4, #05445E);
}
.staff_card:hover .card_side-front{
    transform: rotateY(180deg);
}
.staff_card:hover .card_side-back{
    transform: rotateY(0deg);
}
.backside--name{
    font-size: 1.2rem;
    font-weight: 700;
    text-transform: uppercase;
    letter-spacing: 2px;
    text-align: center;
    line-height: 1.5;
}
.backside--subject{
    font-size: 0.95rem;
    font-weight: 500;
    text-transform: uppercase;
    color:rgba(255,255,255,0.9)
}
.backside--about{
    font-size: 0.8rem;
    font-weight: 400;
    text-align: justify;
    color:rgba(255,255,255,0.8)
}
.hr{
  border:none;
  width: 10%;
  border-top: 5px dotted rgba(255,255,255,0.8);
}
.card_tap_info{
    display: none;
}
@media screen and (max-width:600px) {
    .card_tap_info{
        display: inherit;
        width:20%;
        padding: 5px 5px 5px 10px;
        background-color: #00adb5;
        border-radius: 50%;
        position: absolute;
        top:8px;
        left:8px;
        }
        .card_tap-icon{
        width: 100%;
        }
}

@media screen and (max-width:400px) {
    .staff_card{
        perspective: 1500px;
        -moz-perspective: 1500px;
        width: 250px;
        height:350px;
        position: relative;
        margin: 35px 20px;
    }
    .Staff_section{
        padding: 50px 0;
    }
    .staff_card_container{
        padding-bottom:0px;
    }
    .card_tap_info{
        display: inherit;
        width:20%;
        padding: 5px 5px 5px 10px;
        background-color: #00adb5;
        border-radius: 50%;
        position: absolute;
        top:8px;
        left:8px;
        }
        .card_tap-icon{
        width: 100%;
        }
}
